body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Переменные для Telegram темы */
:root {
  --tg-theme-bg-color: white;
  --tg-theme-text-color: #222;
  --tg-theme-hint-color: #999;
  --tg-theme-link-color: #2678b6;
  --tg-theme-button-color: #2678b6;
  --tg-theme-button-text-color: white;
  --tg-theme-secondary-bg-color: #f5f5f5;
}

/* Переопределение стилей для Telegram тем */
.telegram-theme {
  background-color: var(--tg-theme-bg-color);
  color: var(--tg-theme-text-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Глобальные стили для приложения */
.App {
  min-height: 100vh;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  background-color: white;
}

/* Исправления для мобильных устройств */
html, body {
  width: 100%;
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

/* Стили для лоадера */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Исправление для iOS */
@supports (-webkit-touch-callout: none) {
  .App {
    min-height: -webkit-fill-available;
  }
}

/* Отключение выделения текста, чтобы избежать проблем в Telegram */
* {
  -webkit-tap-highlight-color: transparent;
}

/* Разрешить выделение в input и textarea */
input, textarea {
  user-select: text;
}
